// easy to recognize.

// The `beforeE***REMOVED***pr` property is used to disambiguate between regular
// e***REMOVED***pressions and divisions. It is set on all token types that can
// be followed by an e***REMOVED***pression (thus, a slash after them would be a
// regular e***REMOVED***pression).
//
// The `startsE***REMOVED***pr` property is used to check if the token ends a
// `yield` e***REMOVED***pression. It is set on all token types that either can
// directly start an e***REMOVED***pression (like a quotation mark) or can
// continue an e***REMOVED***pression (like the body of a string).
//
// `isLoop` marks a keyword as starting a loop, which is important
// to know when parsing a label, in order to allow or disallow
// continue jumps to that label.

